import React from 'react';
import { useAppState } from 'shared/state';
import { usePagination } from 'shared/hooks';
import EntityContainer from 'modules/entity/containers';
import {
	NewButton,
	Pagination,
	TableBody,
	TableContainer,
	TableHead
} from 'shared/components/Elements';
import Loader from 'shared/components/Loader';
import {
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import clsx from 'clsx';
import { IconDownload } from 'shared/icons';
import IconButton from '@material-ui/core/IconButton';
import { InvoiceType } from '../../types';
import dayjs from 'dayjs';
import { FaUsers } from 'react-icons/fa';
import { Link } from 'react-router-dom';

const formatDate = (date: string) => (date ? dayjs(date).format('DD/MM/YYYY') : 'No Date');

function Invoices() {
	const { companyId } = useAppState();
	const { page, handlePage } = usePagination();

	return (
		<div>
			{!!companyId && (
				<EntityContainer.All
					entity="invoices"
					name="AllInvoices"
					url={`payment/${companyId}/invoices/`}
					replaceIds
					params={{
						limit: 10,
						extra: {},
						page
					}}
				>
					{({ items, isFetched, meta }) => {
						const invoiceItems = items as InvoiceType[];
						return (
							<>
								{!!invoiceItems.length && (
									<>
										<TableContainer classes={{ root: 'relative ' }}>
											<StyledTable
												aria-label="simple table"
												classes={{ root: 'table-fixed dark-mode-styled-table' }}
											>
												<TableHead>
													<StyledTableRow>
														<StyledTableHeadCell style={{ width: '10%' }}>
															Invoice ID
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '15%' }}>
															Start date
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '15%' }}>
															End date
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '10%' }}>
															Members
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '20%' }}>
															Invoice amount
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '15%' }}>
															Status
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '15%' }}>
															Actions
														</StyledTableHeadCell>
													</StyledTableRow>
												</TableHead>
												<TableBody className="font-poppins text-sm">
													{invoiceItems.map((invoiceItem, idx) => {
														const {
															id,
															start_date,
															end_date,
															staffs_count,
															payment_status,
															amount,
															is_paid,
															is_monthly,
															invoice
														} = invoiceItem;
														return (
															<StyledTableRow key={id}>
																<StyledTableCell className="!font-medium">
																	{`#${id}`}
																</StyledTableCell>
																<StyledTableCell>
																	<span>{formatDate(start_date)}</span>
																</StyledTableCell>
																<StyledTableCell>
																	<span>{formatDate(end_date)}</span>
																</StyledTableCell>
																<StyledTableCell>
																	<div className=" flex-center-y">
																		<FaUsers className="mr-2 text-lg opacity-20" />
																		<span className="font-semibold">{staffs_count}</span>
																	</div>
																</StyledTableCell>
																<StyledTableCell>
																	<strong className="font-semibold">{`$${amount}`}</strong>
																</StyledTableCell>
																<StyledTableCell>
																	<strong
																		className={clsx('font-semibold', {
																			'text-primary-dark': is_paid,
																			'text-kred-pink': !is_paid
																		})}
																	>
																		{payment_status}
																	</strong>
																</StyledTableCell>
																<StyledTableCell>
																	<div className="flex-center-y">
																		<NewButton
																			color={!is_paid ? 'primary' : 'default'}
																			disabled={is_paid}
																			className={clsx('relative w-16', { 'opacity-50': is_paid })}
																		>
																			<Link
																				className="absolute inset-0 dark:text-white"
																				to={`/checkout/${id}/${
																					is_monthly ? '1' : '0'
																				}/${staffs_count}`}
																			>
																				Pay
																			</Link>
																		</NewButton>
																		<IconButton
																			className="!ml-2 h-12 w-12"
																			disabled={!invoice?.url}
																			classes={{ label: 'block absolute inset-0' }}
																		>
																			<a
																				href={invoice?.url}
																				download
																				className="flex-centered absolute inset-0"
																			>
																				<IconDownload
																					className={clsx('text-xl', {
																						'!text-kblue': !is_paid,
																						'!text-kgrey': is_paid
																					})}
																				/>
																			</a>
																		</IconButton>
																	</div>
																</StyledTableCell>
															</StyledTableRow>
														);
													})}
												</TableBody>
											</StyledTable>
										</TableContainer>
									</>
								)}
								{!isFetched && <Loader tiled withOverlay />}
								<div className="news__pagination my-4">
									{items.length ? (
										<Pagination
											count={meta?.last_page}
											page={page ? +page : 1}
											defaultPage={6}
											siblingCount={1}
											color={'primary'}
											onChange={handlePage}
										/>
									) : null}
								</div>
							</>
						);
					}}
				</EntityContainer.All>
			)}
		</div>
	);
}

export default Invoices;
